/* App.css */
.game-info {
    background-color: #003e6a;
    stroke: #177099;
    margin-top: 10px;
    height: 150px;
    overflow: hidden;
    border-radius: 15px;
    box-shadow: inset 0px 0px 20px rgba(255, 255, 255, 0.3);
    cursor: pointer;
    position: relative;
    transition: all 100ms;
    opacity: 0.3;
}

.league-name {
    background-repeat: no-repeat;
    background-position: left;
    height: 50px;
    line-height: 50px;
    text-align: center;
    font-size: 18px;
    font-weight: 600;
    color: #ffffff;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.5);
}

.game-home-name {
    position: absolute;
    display: block;
    right: 50%;
    top: 100px;
    transform: translate(0, -50%);
    margin-right: 150px;
    text-align: right;
    color: #ffffff;
    font-weight: 600;
    font-size: 16px;
    left: 20px;
}

.game-away-name {
    position: absolute;
    display: block;
    left: 50%;
    top: 100px;
    transform: translate(0, -50%);
    margin-left: 150px;
    text-align: left;
    color: #ffffff;
    font-weight: 600;
    font-size: 16px;
    right: 20px;
    /* App.css */
    /* 앞서 작성한 CSS 스타일들... */

    .game_datetime {
        font-size: 16px;
        color: #ffffff;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    .game_home_icon{
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 40px;
        height: 40px;
    }

    .game_home_icon {
        left: 20px;
    }

    .game_away_icon {
        right: 20px;
    }

    .game_home_score,
    .game_away_score,
    .game_vs_score {
        position: absolute;
        font-size: 20px;
        font-weight: bold;
        color: #ffffff;
        top: 50%;
        transform: translateY(-50%);
    }

    .game_home_score {
        left: 75px;
    }

    .game_away_score {
        right: 75px;
    }

    .game_vs_score {
        left: 50%;
        transform: translateX(-50%);
    }

    /* Game.css */
    .game-away-icon {
        position: absolute;
        right: 20px; /* 오른쪽 여백 조정 */
        top: 100px;
        transform: translate(0, -50%);
    }

    /* Game.css */
    .game-datetime {
        text-align: center; /* 텍스트를 가운데 정렬 */
        margin-top: 10px; /* 상단 여백 추가 */
        font-size: 16px; /* 글꼴 크기 조정 */
    }

}

/* 다른 스타일들을 여기에 추가할 수 있습니다. */
